import { createRoot } from "react-dom/client";
import React from "react";
import { BrowserRouter, Routes, Route } from "react-router";
import { Home } from "./Views/home/Home";
import { Videos } from "./Views/videos/Videos";
import { MyGreeting } from "./Views/home/MyGreeting";
import { Domain } from "./Views/Domain";
import { getRootPath } from "./utils/getRootPath";
import { ResponsiveDesign } from "./Views/ResponsiveDesign";
import { ReactFeedbackPractice } from "./Views/ReactFeedbackPractice";
import { ReactRenderPractice } from "./Views/ReactRenderPractice";
import { HandleRefresh } from "./Views/navbar/HandleRefresh";
import { Header } from "./Views/Header";
import { Footer } from "./Views/Footer";
import { Server } from "./Views/Server";

// const root = createRoot(window.bodyTag);
const bodyTag = document.getElementById("bodyTag");
const root = createRoot(bodyTag);
const rootPath = getRootPath();

root.render(
	<BrowserRouter>
		<HandleRefresh>
			<Header />
			<Routes>
				{/* <Route
					path={`${rootPath}/`}
					element={<Home />}
				/> */}
				<Route
					path={`${rootPath}/home`}
					element={<Home />}
				/>
				<Route
					path={`${rootPath}/react-feedback-practice`}
					element={<ReactFeedbackPractice />}
				/>
				<Route
					path={`${rootPath}/react-render-practice`}
					element={<ReactRenderPractice />}
				/>
				<Route
					path={`${rootPath}/server`}
					element={<Server />}
				/>
				<Route
					path={`${rootPath}/videos`}
					element={<Videos />}
				/>
				<Route
					path={`${rootPath}/domain`}
					element={<Domain />}
				/>
				<Route
					path={`${rootPath}/responsive`}
					element={<ResponsiveDesign />}
				/>
			</Routes>
			<Footer />
		</HandleRefresh>
	</BrowserRouter>
);